  .footer__item {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.footer__item span {
  color: #939393;
}

.footer__item:not(:first-child) {
  padding-left: 20px;
}

.footer__item:not(:first-child):before {
  content: '';
  position: absolute;
  top: 50%;
  left: 9px;
  width: 1px;
  height: 12px;
  transform: translateY(-50%);
  background-color: #292929;
}

.footer-menu {
  display: flex;
  align-items: center;
}

.footer-menu__item {
  position: relative;
  padding-left: 23px;
}

.footer-menu__item:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 10px;
  width: 1px;
  height: 12px;
  transform: translateY(-50%);
  background-color: #292929;
}

@media only screen and (max-width: 599px) {
  .footer-menu__item:first-child {
    padding-left: 0;
  }
  .footer-menu__item:first-child:before {
    display: none;
  }
}

.footer-menu__link {
  text-decoration: none;
  color: #555;
  transition: color .3s;
}

.no-touch .footer-menu__link:hover {
  color: #c0b099;
}
